<template>
    <div class="carousel-inner">
        <div v-for="(slider,key) in sliderWithCaption" class="carousel-item" :class="slider.active ? 'active' : ''"
             :key="key">
            <img :src="slider.src" class="d-block w-100" alt="..."/>
            <div class="carousel-caption d-none d-md-flex flex-md-column">
                <div class="p-2">
                    {{ slider.description }}
                </div>
                <div class="p-2">
                    <Modal :modalName="componentDescription + key" :componentName="componentDescriptionName"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Modal from "@/components/Layout/Modal";
import SecondWarDescription from "@/components/Pages/SecondWar/SecondWarDescription";

const DESCRIPTIONS = {"SecondWarDescription": SecondWarDescription};

export default {
    props: ['sliderWithCaption', 'descriptionLink', 'componentDescription'],
    components: {
        Modal,
    },
    data() {
        return {
            componentDescriptionName: DESCRIPTIONS[this.componentDescription]
        }
    }
}
</script>
